import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ImagenFondoComponent } from './imagen-fondo.component';
import { MgComponent } from './mg/mg.component';
import { AimaComponent } from './aima/aima.component';

export const routes: Routes = [
  { 
    path: 'mg', 
    component: MgComponent,
  },
  { 
    path: 'ai', 
    component: AimaComponent,
  },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })
  export class ImagenFondoRoutingModule { }